import React from 'react';
import AppContext from './context';
import MenuBar from './components/MenuBar/MenuBar/MenuBar';
import GlobalStyle from './GlobalStyle/GlobalStyle';
import Desktop from './components/Desktop/Desktop';

import pc from './assets/img/komputer.png';
import bin from './assets/img/kosz.png';
import IE from './assets/img/IE.png';
import paint from './assets/img/paint.png';
import dysk from './assets/img/dysk.png';
import folder from './assets/img/folder.png';

class App extends React.Component {
  state = {
    scale: 0,
    scale2ndBar: 0,
    Data: [
      {
        key: 0,
        content: 'Komputer',
        desk: true,
        src: pc,
        active: 1,
        functions: ['File', 'Edit', 'View'],
        initialPos: {
          x: 0,
          y: 0,
        },
      },
      {
        key: 1,
        content: 'Kosz',
        desk: true,
        src: bin,
        active: 1,
        functions: ['File', 'Edit', 'View'],
        initialPos: {
          x: 0,
          y: 65,
        },
      },
      {
        key: 2,
        content: 'Paint',
        desk: true,
        src: paint,
        active: 1,
        functions: ['File', 'About'],
        initialPos: {
          x: 0,
          y: 130,
        },
      },
      {
        key: 3,
        content: 'Internet Explorer',
        desk: true,
        src: IE,
        active: 1,
        functions: [],
        initialPos: {
          x: 0,
          y: 185,
        },
      },
    ],
    activePrograms: [],
    inputValue: ['/', '/', '/', '/'],
    path: ['', '', '', ''],
  };

  paths = [
    '/a',
    '/a/',
    '/c',
    '/c/',
    '/c/kosz',
    '/c/kosz/',
    '/c/folder',
    '/c/folder/',
    '/',
    '/a/desktop',
    '/a/desktop/',
  ];

  deactivateMenuBarIcon = () => {
    const { activePrograms } = this.state;
    activePrograms.forEach(program => {
      program.active = 0;
    });
  };

  startBarFn = () => {
    this.deactivateMenuBarIcon();

    const { scale } = this.state;

    if (scale === 0) this.setState({ scale: 1 });
    else if (scale === 1) this.setState({ scale: 0, scale2ndBar: 0 });
  };

  startBarFn2 = () => {
    const { scale2ndBar } = this.state;
    if (scale2ndBar === 0) this.setState({ scale2ndBar: 1 });
    else if (scale2ndBar === 1) this.setState({ scale2ndBar: 0 });
  };

  closeBarFn = () => {
    this.deactivateMenuBarIcon();

    this.setState({
      scale: 0,
      scale2ndBar: 0,
    });
  };

  closeMoreFn = () => {
    this.setState({
      scale2ndBar: 0,
    });
  };

  openProgramFn = id => {
    const { Data, activePrograms } = this.state;
    const DataArray = [...Data];
    this.deactivateMenuBarIcon();

    if (activePrograms.includes(Data[id]) === false) {
      this.setState(prevState => ({
        activePrograms: [...prevState.activePrograms, Data[id]],
      }));
    }
    DataArray[id].active = 1;

    this.setState({
      Data: DataArray,
    });
  };

  closeProgram = key => {
    const { activePrograms } = this.state;
    const activeProgramsArray = [...activePrograms];
    const index = activeProgramsArray.findIndex(program => program.key === key);
    activeProgramsArray.splice(index, 1);

    this.setState({
      activePrograms: activeProgramsArray,
    });
  };

  /* window */

  changePath = (e, id) => {
    const { inputValue } = this.state;
    const copyValue = inputValue;
    copyValue[id] = e.target.value;
    this.setState({
      inputValue: copyValue,
    });
  };

  keyDown = (e, id) => {
    if (e.key === 'Enter') {
      this.onSubmit(id);
    }
  };

  onSubmit = (id, pathIcon) => {
    const { inputValue, path } = this.state;
    const { paths } = this;
    const actualValue = path;
    if (path[id] === inputValue[id]) {
      actualValue[id] = pathIcon;
    } else {
      actualValue[id] = inputValue;
    }
    const index = paths.findIndex(correct => actualValue[id] === correct);
    if (index !== -1) {
      this.setState({
        path: actualValue,
        inputValue: actualValue,
      });
    }
  };

  handleBack = id => {
    const { path } = this.state;
    const pathC = path;
    const word = pathC[id].split('');
    if (path[id] !== '/') {
      const pathR = word.reverse();
      const index = pathR.findIndex(ind => ind === '/');
      if (pathR.length === 2) {
        pathR.splice(0, index);
      } else {
        pathR.splice(0, index + 1);
      }
      pathC[id] = pathR.reverse().join('');
      this.setState({
        path: pathC,
        inputValue: pathC,
      });
    }
  };

  createTitle = id => {
    const { path } = this.state;
    let pathC = path[id].split('');
    if (path[id] !== '/') {
      const pathR = pathC.reverse();
      const index = pathR.findIndex(ind => ind === '/');
      pathR.splice(index, pathR.length - 1);
      pathC = pathR.reverse().join('');
    }
    return pathC;
  };

  createImg = id => {
    const { path } = this.state;
    let src;
    switch (path[id]) {
      case '/a':
      case '/a/':
      case '/c':
      case '/c/':
        src = dysk;
        break;
      case '/a/desktop':
      case '/a/desktop/':
        src = folder;
        break;
      case '/c/kosz':
        src = bin;
        break;
      case '/':
        src = pc;
        break;
      default:
        src = null;
    }
    return src;
  };

  handleHome = id => {
    const { path } = this.state;
    const pathC = path;
    pathC[id] = '/';
    this.setState({
      path: pathC,
      inputValue: pathC,
    });
  };

  setInitPath = (programName, id) => {
    const { path } = this.state;
    const pathC = path;
    if (programName === 'Kosz') {
      pathC[id] = '/c/kosz';
    }
    if (programName === 'Komputer') {
      pathC[id] = '/';
    }
    this.setState({
      path: pathC,
      inputValue: pathC,
    });
  };

  render() {
    const { activePrograms, Data } = this.state;
    const { paths } = this;
    return (
      <AppContext.Provider
        value={{
          state: this.state,
          startBarFn: this.startBarFn,
          startBarFn2: this.startBarFn2,
          closeMoreFn: this.closeMoreFn,
          openProgramFn: this.openProgramFn,

          handleHome: this.handleHome,
          createImg: this.createImg,
          createTitle: this.createTitle,
          handleBack: this.handleBack,
          onSubmit: this.onSubmit,
          changePath: this.changePath,
          keyDown: this.keyDown,
          setInitPath: this.setInitPath,
        }}
      >
        <div className="App">
          <GlobalStyle />
          <MenuBar data={activePrograms} openProgramFn={this.openProgramFn} />
          <GlobalStyle />
          <Desktop
            clickFn={this.closeBarFn}
            data={Data}
            openProgramFn={this.openProgramFn}
            closeProgramFn={this.closeProgram}
            activePrograms={activePrograms}
            paths={paths}
          />
        </div>
      </AppContext.Provider>
    );
  }
}

export default App;
