import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const StyledBar = styled.section`
  height: 20px;
  width: 100%;
  position: relative;
  display: flex;
`;

const StyledButtonSmall = styled.button`
  height: 100%;
  background-color: lightgray;
  border: 1px solid gray;
  width: 15px;
`;
const StyledButtonBig = styled.button`
  height: 100%;
  background-color: lightgray;
  border: 1px solid gray;
  width: 25px;
`;

const StyledInput = styled.input`
  font-size: 1.5rem;
  position: relative;
  width: 100%;
  height: 100%;
`;

const Pathbar = ({ inputValue, changePath, keyDown, back, id }) => {
  return (
    <StyledBar>
      <StyledButtonSmall onClick={() => back(id)}>{'<'}</StyledButtonSmall>
      <StyledButtonBig />
      <StyledInput
        value={inputValue}
        onKeyDown={e => keyDown(e, id)}
        onChange={e => changePath(e, id)}
        changePath={e => changePath(e, id)}
      />
      <StyledButtonBig />
    </StyledBar>
  );
};

Pathbar.propTypes = {
  inputValue: PropTypes.string.isRequired,
  changePath: PropTypes.func.isRequired,
  keyDown: PropTypes.func.isRequired,
  back: PropTypes.func.isRequired,
  id: PropTypes.number.isRequired,
};

export default Pathbar;
