import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

/* ogarnąć połączenie window - menubar */

import FnOpenButton from './FunctionsBar/FnOpenButton';
import Pathbar from './Pathbar';
import Content from './Programs/Content';
import IE from './Programs/IE';
import Paint from './Programs/Paint';

const StyledWindow = styled.article`
  height: 300px;
  width: 300px;
  background-color: #c0c0c0;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  border-top: 2px solid #dfdfdf;
  border-left: 2px solid #dfdfdf;
  position: absolute;
  z-index: ${props => (props.active ? '3' : '2')};
  /* z-index:3; */
  top: 30%;
  left: 30%;
  display: flex;
  flex-direction: column;
  padding: 2px;
  ${({ dragging }) => dragging && `cursor:grabbing`}
`;

const StyledTitle = styled.section`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 30px;
  background-image: linear-gradient(to right, #fd02ff, #8876ff, #08f6ff);
  padding-left: 5px;
`;

const StyledFlexBoxWrapper = styled.section`
  display: flex;
`;

const StyledImg = styled.img`
  display: block;
  height: 20px;
  width: 20px;
`;

const StyledH2 = styled.h2`
  margin-left: 5px;
  font-size: 2rem;
  color: white;
`;

const StyledNavItem = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  width: 20px;
  font-size: 2rem;
  background-color: #c0c0c0;
  padding: 0;
  margin: 1px;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  border-top: 2px solid #dfdfdf;
  border-left: 2px solid #dfdfdf;
`;

const StyledFuncitonBar = styled.section`
  height: 20px;
  width: 100%;
  display: flex;
  align-items: center;
`;

const StyledContent = styled.section`
  font-size: 2rem;
  width: 100%;
  flex-grow: 1;
  background-color: white;
  position: relative;
`;

class Window extends React.Component {
  ref = React.createRef();

  constructor(props) {
    super(props);
    this.state = {
      activeBars: [],
      pos: { x: Math.floor(Math.random() * 1000), y: Math.floor(Math.random() * 500) },
      dragging: false,
      rel: null,
    };
  }

  componentDidMount() {
    const { programName, setInitPath, id } = this.props;
    setInitPath(programName, id);
  }

  componentDidUpdate(props, state) {
    const { dragging } = this.state;
    if (dragging && !state.dragging) {
      document.addEventListener('mousemove', this.onMouseMove);
      document.addEventListener('mouseup', this.onMouseUp);
    } else if (!dragging && state.dragging) {
      document.removeEventListener('mousemove', this.onMouseMove);
      document.removeEventListener('mouseup', this.onMouseUp);
    }
  }

  onMouseDown = e => {
    const { ref } = this;
    if (e.button !== 0) return;
    const posX = ref.current.offsetLeft;
    const posY = ref.current.offsetTop;
    this.setState({
      dragging: true,
      rel: {
        x: e.pageX - posX,
        y: e.pageY - posY,
      },
    });
    e.stopPropagation();
    e.preventDefault();
  };

  onMouseUp = e => {
    this.setState({ dragging: false });
    e.stopPropagation();
    e.preventDefault();
  };

  onMouseMove = e => {
    const { dragging, rel } = this.state;
    if (!dragging) return;
    const pos = {
      x: e.pageX - rel.x,
      y: e.pageY - rel.y,
    };
    this.setState({
      pos,
    });
    e.stopPropagation();
    e.preventDefault();
  };

  handleOpenedBtn = id => {
    const { activeBars } = this.state;
    const activeBarsCopy = activeBars;
    const prevActive = activeBars[id];
    for (let i = 0; i < activeBarsCopy.length; i += 1) {
      activeBarsCopy[i] = false;
    }
    activeBarsCopy[id] = !prevActive;
    this.setState({
      activeBars: activeBarsCopy,
    });
  };

  render() {
    const {
      programName,
      id,
      imgSrc,
      closeProgramFn,
      active,
      functions,
      handleHome,
      createImg,
      createTitle,
      handleBack,
      onSubmit,
      changePath,
      keyDown,
      path,
      inputValue,
      paths,
    } = this.props;
    const { activeBars, dragging, pos } = this.state;
    const { handleOpenedBtn, onMouseDown, ref } = this;

    /* conditions */
    const condition = functions === undefined || functions.length === 0;
    const conditionBar = programName === 'Kosz' || programName === 'Komputer';
    const findIndex = paths.findIndex(pathe => pathe === path);
    const checkIE = programName === 'Internet Explorer' ? <IE /> : null;
    const checkContent =
      findIndex !== -1 ? <Content onSubmit={onSubmit} id={id} path={path} /> : null;
    const checkPaint = programName === 'Paint' ? <Paint /> : null;
    const functionBarContent = condition
      ? null
      : functions.map((title, i) =>
          title ? (
            <FnOpenButton
              id={i}
              handleOpenedBtn={handleOpenedBtn}
              active={activeBars[i]}
              key={title}
              title={title}
            />
          ) : (
            ''
          ),
        );

    return (
      <>
        <StyledWindow
          style={{ left: `${pos.x}px`, top: `${pos.y}px` }}
          onMouseDown={onMouseDown}
          ref={ref}
          dragging={dragging}
          id={id}
          active={active}
        >
          <StyledTitle>
            <StyledFlexBoxWrapper>
              <StyledImg src={path !== '' ? createImg(id) : imgSrc} />
              <StyledH2>{conditionBar ? createTitle(id) : programName}</StyledH2>
            </StyledFlexBoxWrapper>
            <StyledFlexBoxWrapper>
              <StyledNavItem>_</StyledNavItem>
              <StyledNavItem>O</StyledNavItem>
              <StyledNavItem onClick={closeProgramFn}>X</StyledNavItem>
            </StyledFlexBoxWrapper>
          </StyledTitle>
          {condition ? null : <StyledFuncitonBar>{functionBarContent}</StyledFuncitonBar>}
          {conditionBar ? (
            <Pathbar
              path={path}
              inputValue={inputValue}
              onSubmit={onSubmit}
              changePath={changePath}
              keyDown={keyDown}
              back={handleBack}
              home={handleHome}
              id={id}
            />
          ) : null}
          <StyledContent>{checkIE || checkContent || checkPaint}</StyledContent>
        </StyledWindow>
      </>
    );
  }
}

Window.propTypes = {
  id: PropTypes.number.isRequired,
  programName: PropTypes.string,
  imgSrc: PropTypes.string,
  active: PropTypes.number.isRequired,
  closeProgramFn: PropTypes.func.isRequired,
  functions: PropTypes.arrayOf(PropTypes.string.isRequired).isRequired,
  setInitPath: PropTypes.func.isRequired,
  handleHome: PropTypes.func.isRequired,
  handleBack: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  createImg: PropTypes.func.isRequired,
  createTitle: PropTypes.func.isRequired,
  changePath: PropTypes.func.isRequired,
  keyDown: PropTypes.func.isRequired,
  path: PropTypes.string.isRequired,
  paths: PropTypes.string.isRequired,
  inputValue: PropTypes.string.isRequired,
};

Window.defaultProps = {
  programName: 'Program',
  imgSrc: null,
};

export default Window;
