import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import StorageACon from './Contents/StorageACon';
import StorageCCon from './Contents/StorageCCon';
import StartCon from './Contents/StartCon';
import BinCon from './Contents/BinCon';
import DesktopFCon from './Contents/DesktopFCon';

const StyledContent = styled.section`
  font-size: 2rem;
  width: 100%;
  height: 100%;
  background-color: white;
  position: relative;
`;

const Content = ({ path, onSubmit, id }) => {
  let mainContent;
  switch (path) {
    case '/':
      mainContent = <StartCon onSubmit={onSubmit} id={id} />;
      break;
    case '/c/kosz':
    case '/c/kosz/':
      mainContent = <BinCon onSubmit={onSubmit} id={id} />;
      break;
    case '/a':
    case '/a/':
      mainContent = <StorageACon onSubmit={onSubmit} id={id} />;
      break;
    case '/a/desktop':
    case '/a/desktop/':
      mainContent = <DesktopFCon />;
      break;
    case '/c':
    case '/c/':
      mainContent = <StorageCCon onSubmit={onSubmit} id={id} />;
      break;
    default:
      return '';
  }
  return <StyledContent>{mainContent}</StyledContent>;
};

Content.propTypes = {
  path: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired,
  onSubmit: PropTypes.func.isRequired,
};

export default Content;
